import Vue from 'vue'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import IcCamPTZ from '@/assets/svg/cameras/ic-cam-PTZ.svg?inline'
import IcCamNotPTZ from '@/assets/svg/cameras/ic-cam-notPTZ.svg?inline'
import VCheckbox from '@/components/ui/v-checkbox/v-checkbox.vue'
import VInput from '@/components/ui/v-input/v-input.vue'
import VSelect from '@/components/ui/v-select/v-select.vue'

Vue.component('scrollbar', {
  extends: VuePerfectScrollbar,
  props: {
    settings: {
      default: () => ({
        minScrollbarLength: 20
      })
    }
  }
})
Vue.component('ic-cam-PTZ', IcCamPTZ)
Vue.component('ic-cam-notPTZ', IcCamNotPTZ)
Vue.component('v-checkbox', VCheckbox)
Vue.component('v-input', VInput)
Vue.component('v-select', VSelect)
