import { mapActions, mapMutations, mapState } from 'vuex'
import { defineComponent } from '@vue/composition-api'
import cloneDeep from 'lodash.clonedeep'
import { useStatusDate } from '@/components/composition/format-utils'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import Modal from '@/components/modal/modal.vue'
import ConfirmAlertModal from '@/components/confirm-alert-modal/confirm-alert-modal.vue'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import LoaderBtn from '@/components/loader-btn/loader-btn.vue'
import IcClear from '@/assets/svg/ic-clear.svg?inline'
import IcEdit from '@/assets/svg/ic-edit.svg?inline'
import IcSave from '@/assets/svg/ic-save.svg?inline'
import IcRefresh from '@/assets/svg/ic-refresh.svg?inline'

const defaultResolutionStr = '0 : 0'

export default defineComponent({
  name: 'PassportModal',
  props: {
    camera: {
      type: Object
    }
  },
  components: {
    ValidationObserver,
    ValidationProvider,
    Modal,
    ConfirmAlertModal,
    FixFluidColGrid,
    LoaderBtn,
    IcClear,
    IcEdit,
    IcSave,
    IcRefresh
  },
  setup(props, context) {
    return {
      getStatusDate: useStatusDate(context.root.$moment)
    }
  },
  data() {
    return {
      cameraObj: null,
      passportObjEdit: null,
      resolutionStr: defaultResolutionStr,
      resolutionStrEdit: defaultResolutionStr,
      isNewPassport: true,
      editEnabled: false,
      refreshLoading: false
    }
  },
  computed: {
    ...mapState({
      updateLoading: state => state.update.loading,
    })
  },
  methods: {
    ...mapActions({
      createUpdatePassport: 'cameras/createUpdatePassport',
      inOutExploitation: 'cameras/inOutExploitation',
      updateCameraInfo: 'cameras/updateCameraInfo'
    }),
    ...mapMutations({
      setGlobalLoading: 'SET_GLOBAL_LOADING',
      setReportsFilter: 'reports/SET_FILTER'
    }),
    setCameraData(cameraData) {
      const result = cloneDeep(cameraData)
      if (!result.passport) {
        result.passport = {
          fps: '',
          kbps: '',
          width: 0,
          height: 0
        }
      }
      return result
    },
    setResolutionStr(passport) {
      let width = 0
      let height = 0
      if (passport) {
        width = passport.width
        height = passport.height
      }
      return `${width} : ${height}`
    },
    setStartData(cameraData) {
      this.cameraObj = this.setCameraData(cameraData)
      this.passportObjEdit = { ...this.cameraObj.passport }
      this.resolutionStr = this.setResolutionStr(this.cameraObj.passport)
      this.resolutionStrEdit = this.setResolutionStr(this.cameraObj.passport)
      this.isNewPassport = true
      if (cameraData.passport) {
        this.isNewPassport = false
      }
    },
    show () {
      this.$refs.modal.show()
      if (this.camera) {
        /**
         * Сразу подставляем значения и смотрим не пришли ли новые данные
         */
        this.setStartData(this.camera)
        this.updateInfo()
      }
    },
    hide () {
      let isChanged = false
      const pasportKeys = Object.keys(this.passportObjEdit)
      for (let i = 0; i < pasportKeys.length; i++) {
        const key = pasportKeys[i]
        if (this.cameraObj.passport[key] !== this.passportObjEdit[key]) {
          isChanged = true
          break
        }
      }
      if (isChanged) {
        this.$refs['confirm-alert-modal'].show()
      } else {
        this.$refs.modal.hide()
      }
    },
    onClose() {
      this.editEnabled = false
      this.$emit('close')
    },
    onCloseWithoutSave() {
      this.cancelEdit()
      this.hide()
    },
    enabledEdit() {
      this.editEnabled = true
    },
    cancelEdit() {
      this.passportObjEdit = { ...this.cameraObj.passport  }
      this.resolutionStrEdit = this.setResolutionStr(this.cameraObj.passport)
      this.editEnabled = false
    },
    onInputResolution() {
      const [ width, height ] = this.resolutionStrEdit.split(' : ')
      .map(item => +item.replace(/_/g, ''))
      this.passportObjEdit.width = width
      this.passportObjEdit.height = height
    },
    onFocusResolution() {
      if (this.resolutionStrEdit === defaultResolutionStr) {
        this.resolutionStrEdit = ''
      }
    },
    onPassportSubmit() {
      this.$refs['passport-form'].validate().then(async success => {
        if (success) {
          this.setGlobalLoading(true)
          try {
            const res = await this.createUpdatePassport({
              data: {
                id: this.cameraObj.id,
                data: {
                  ...this.passportObjEdit
                }
              },
              isCreate: this.isNewPassport
            })
            /**
             * не все поля включены в ответ,
             * поэтому делаем объединение и новые поля перезапишутся
             */
            this.setStartData({ ...this.cameraObj, ...res })
            this.editEnabled = false
            this.$eventBus.$emit('bus:passport-change')
          } finally {
            this.setGlobalLoading(false)
          }
        }
      })
    },
    async onInOutExploitation() {
      this.setGlobalLoading(true)
      try {
        const res = await this.inOutExploitation({
          id: this.cameraObj.id,
          statusExploitation: this.cameraObj.statusExploitation
        })
        this.setStartData({ ...this.cameraObj, ...res })
        this.$eventBus.$emit('bus:passport-change')
      } finally {
        this.setGlobalLoading(false)
      }
    },
    async updateInfo(isRefresh) {
      if (isRefresh) {
        this.refreshLoading = true
      } else {
        this.setGlobalLoading(true)
      }
      
      try {
        const res = await this.updateCameraInfo(this.cameraObj.id)
        this.setStartData(res)
        this.editEnabled = false
      } finally {
        if (isRefresh) {
          this.refreshLoading = false
        } else {
          this.setGlobalLoading(false)
        }
      }
    },
    onGoToReports() {
      this.setReportsFilter({ key: 'isSelectAll', value: false })
      this.setReportsFilter({ key: 'selectedCameras', value: [ this.cameraObj ] })
      this.$refs.modal.hide()
      this.$router.push({
        name: 'reports'
      })
    }
  },
  watch: {
    updateLoading() {
      if (!this.updateLoading && this.camera) {
        this.updateInfo(true)
      }
    },
  }
})