import { defineComponent } from '@vue/composition-api'
import { mapState, mapActions, mapMutations } from 'vuex'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import SelectInterval from '@/components/select-interval/select-interval.vue'
import Loader from '@/components/loader/loader.vue'
import LoaderBtn from '@/components/loader-btn/loader-btn.vue'
import StatisicChart from '@/components/statistic-chart/statistic-chart.vue'
import IcSelect from '@/assets/svg/ic-select.svg?inline'
import Stub from '@/components/stub/stub.vue'
import { SELECT_ALL } from '@/consts/select-all-option'
import { formatNumber, statisticTimeFormat } from '@/components/composition/format-utils'

/**
 * dataToShow по запросу getReport имеет следующую структуру
 * {
 *   // intervals - массив объектов { start: String, end: String, value: string }
 *   // timeUnit - по спеке https://www.chartjs.org/docs/latest/axes/cartesian/time.html
 *   workingTime: {
 *     intervals: [],
 *     // часы:минуты
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   workingTimePercent: {
 *     intervals: [],
 *     // отличие от времени в разделителе
 *     totalValue: '0.0',
 *     timeUnit: 'week'
 *   },
 *   problemTime: {
 *     intervals: [],
 *     // часы:минуты
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   notWorkingCameraCount: 0,
 *   totalTime: '0:0'
 * }
 */



export default defineComponent({
  name: 'Reports',
  components: {
    ValidationObserver,
    ValidationProvider,
    FixFluidColGrid,
    Loader,
    LoaderBtn,
    StatisicChart,
    SelectInterval,
    IcSelect,
    Stub
  },
  setup() {
    const exportOptions = [
      {
        id: 'xlsx',
        name: 'Excel'
      },
      {
        id: 'html',
        name: 'html'
      }
    ]
    const fieldsStatisticCommon = ['totalTime', 'notWorkingCameraCount']
    const filedsChart = ['workingTime', 'workingTimePercent', 'problemTime', 'archiveTime']
    return {
      exportOptions,
      fieldsStatisticCommon,
      filedsChart,
      formatNumber,
      statisticTimeFormat
    }
  },
  data() {
    return {
      dataToShow: null,
      firstSerachFlag: false
    }
  },
  computed: {
    ...mapState({
      /**
       * 
       * @param {*} { selectedCameras, rangeOfDate }
       */
      filters: state => state.reports.filters,
      loading: state => state.reports.loading,
      exportLoading: state => state.reports.exportLoading
      
    }),
    totalTimeView() {
      if (this.dataToShow) {
        const result = this.statisticTimeFormat(this.dataToShow.totalTime)
        result.hours = this.formatNumber(result.hours)
        return result
      }
    }
  },
  beforeDestroy() {
    this.dataToShow = null
    this.firstSerachFlag = false
  },
  methods: {
    ...mapActions({
      getReport: 'reports/getReport'
    }),
    ...mapMutations({
      setFilter: 'reports/SET_FILTER'
    }),
    setSelectedCameras(event) {
      if (Array.isArray(event) && event.length === 0) {
        this.setFilter({ key: 'isSelectAll', value: false })
      } else if (event === SELECT_ALL) {
        this.setFilter({ key: 'isSelectAll', value: true })
      }
      this.setFilter({ key: 'selectedCameras', value: event })
    },
    setRangeOfDate(event) {
      this.setFilter({key: 'rangeOfDate', value: event})
    },
    onFilterSubmit() {
      this.$refs.filterForm.validate().then(async result => {
        if (result) {
          const res = await this.getReport()
          this.dataToShow = res
          this.firstSerachFlag = true
        }
      })
    },
    downloadReport(exportOption) {
      this.$refs.filterForm.validate().then(result => {
        if (result) {
          this.getReport(exportOption.id)
        }
      })
    }
  },
  watch: {
    '$i18n.locale'() {
      if (this.$refs.filterForm.flags.validated) {
        this.$refs.filterForm.validate()
      }
    }
  }
})