import { ref, onMounted, onBeforeUnmount } from '@vue/composition-api'
import debounce from 'debounce'

export function useDropdown() {
  const dropdown = ref(null)
  const open = ref(false)

  const toggleOpen = () => {
    open.value = !open.value
  }

  const clickOutside = e => {
    if (!dropdown.value.contains(e.target)) {
      open.value = false
    }
  }

  onMounted(() => {
    window.addEventListener('click', clickOutside)
  })

  onBeforeUnmount(() => {
    window.removeEventListener('click', clickOutside)
  })

  return {
    dropdown,
    open,
    clickOutside,
    toggleOpen
  }
}

/**
 * 
 * refs - open, currentOptions,
 */
export function useFetchByScroll(remoteFn, open = { value: true }, isLoadMounted = true) {
  const currentOptions = ref([])

  const resultState = {
    isFetching: false,
    isFetched: false,
    cancelToken: null
  }

  const total = ref(0)

  /**
   * +Могут быть другие параметры
   */
  const queryParamsSaved = {
    /**
     * пока жестко зададим limit,
     * если нужно будет - сделаем параметром
     */
    limit: 30,
    offset: 0,
  }

  /*END pagination state */
  /**
   * 
   * @param {*} queryParams = { limit, offset }
   */
  const fetchItems = async (queryParams) => {
    let checkParamsEqual = true
    const keysParams = Object.keys(queryParams)
    for (let i = 0; i < keysParams.length; i++) {
      const key = keysParams[i]
      if (queryParamsSaved[key] !== queryParams[key]) {
        checkParamsEqual = false
        break
      }
    }
    if (checkParamsEqual && resultState.isFetching) {
      return
    }
    if (!resultState.isFetched || !checkParamsEqual) {
      const params = { ...queryParams }
      if (!params.q) {
        delete params.q
      }
      /* { req, cancelToken } */
      const result = await remoteFn(params)
      resultState.cancelToken = result.cancelToken
      resultState.isFetching = true
      try {
        const res = await result.req
        if (res.list) {
          if (res.pagination.offset === 0) {
            currentOptions.value = res.list
          } else {
            currentOptions.value = [...currentOptions.value, ...res.list]
          }
        }
        total.value = res.pagination.total
        queryParamsSaved.limit = res.pagination.limit
        queryParamsSaved.offset = res.pagination.offset
        resultState.isFetched = true
      } finally {
        resultState.isFetching = false
      }
      
    }
  }

  const resetResults = () => {
    if (resultState.cancelToken) {
      resultState.cancelToken()
    }
    resultState.isFetched = false
    queryParamsSaved.offset = 0
    total.value = 0
  }

  onMounted(() => {
    if (remoteFn && isLoadMounted) {
      fetchItems(queryParamsSaved)
    }
  })

  const onScrollEnd = debounce(() => {
    if (remoteFn && open.value) {
      const nextParams = {...queryParamsSaved}
      let nextOffset = nextParams.offset + nextParams.limit
      if (nextOffset > total.value) {
        nextOffset = total.value
      }
      nextParams.offset = nextOffset
      fetchItems(nextParams)
    }
  }, 300)

  const onScrollUp = () => {
    if (remoteFn) {
      resultState.isFetched = false
    }
  }

  return {
    currentOptions,
    resultState,
    queryParamsSaved,
    fetchItems,
    resetResults,
    total,
    onScrollEnd,
    onScrollUp
  }
}