import { defineComponent } from '@vue/composition-api'
import Modal from '@/components/modal/modal.vue'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import DateTime from '@/components/date-time/date-time.vue'
import IcClear from '@/assets/svg/ic-clear.svg?inline'

/**
 * Пришлось записать в опциональном стиле, 
 * так как ref observer не опрделялся
 * Все что внутри modal нужно писать в опциональном стиле
 */
export default defineComponent({
  name: 'TimeIntervalForm',
  components: {
    Modal,
    ValidationObserver,
    ValidationProvider,
    DateTime,
    IcClear
  },
  data() {
    return {
      form: {
        startDate: null,
        endDate: null
      },
      notMatchError: false,
      /**
       * для ошибок с длинным текстом
       */
      formStyles: null
    }
  },
  created() {
    this.setDefaultValues()
  },
  methods: {
    setDefaultValues() {
      this.form.startDate = this.$moment().subtract(1, 'hour'),
      this.form.endDate = this.$moment()
    },
    submit() {
      this.$refs.observer.validate().then(success => {
        if (!success) {
          return
        }
        if (this.form.endDate.diff(this.form.startDate) < 0) {
          this.notMatchError = true
          return
        }
        this.$emit('submit', this.form)
        this.hide()
      })
    },
    onInput() {
      this.notMatchError = false
    },
    show () {
      this.$refs.modal.show()
      this.$nextTick(() => {
        const width = this.$refs.observer.$el.getBoundingClientRect().width
        this.formStyles = {
          width: width + 'px'
        }
      })
    },
    hide () {
      this.$refs.modal.hide()
    },
    onClose() {
      this.formStyles = null
      this.notMatchError = false
      this.setDefaultValues()
      this.$refs.observer.reset()
    }
  },
  watch: {
    '$i18n.locale'() {
      if (this.form.startDate) {
        this.form.startDate.locale(this.$i18n.locale)
      }
      if (this.form.endDate) {
        this.form.endDate.locale(this.$i18n.locale)
      }
    }
  }
})