import { createI18n } from 'vue-i18n-composable/dist/index.js'
import localMsgs from './locales'

const userLocale = new Intl.DateTimeFormat().resolvedOptions().locale


export const startLocale = userLocale.indexOf('ru') !== -1 ? 'ru' : 'en'

export const i18n = createI18n({
  locale: startLocale,
  messages: localMsgs,
  pluralizationRules: {
    'ru': function(choice = 0) {
      return choice
    },
    'en': function(choice = 0) {
      return choice
    },
  }
})
