import debounce from 'debounce'
import { defineComponent, ref, onMounted } from '@vue/composition-api'
import { mapState, mapActions, mapMutations } from 'vuex'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import Paginate from 'vuejs-paginate'
import { useStatusDate } from '@/components/composition/format-utils'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import HeaderChart from '@/components/header-chart'
import VTable from '@/components/table/table'
import Loader from '@/components/loader/loader.vue'
import LoaderBtn from '@/components/loader-btn/loader-btn.vue'
import PassportModal from '@/components/passport-modal/passport-modal.vue'
import Stub from '@/components/stub/stub.vue'
import { CAMERA_STATUSES, EXPLOITATION_STATUSES } from '@/consts/statuses'
import { SELECT_ALL } from '@/consts/select-all-option'
import IcSearch from '@/assets/svg/ic-search.svg?inline'
import IcRefresh from '@/assets/svg/ic-refresh.svg?inline'
import IcReport from '@/assets/svg/ic-report.svg?inline'

/**
 * переписывание на composition только усложнит
 * логику так как состояние компонента полностью
 * регулируется стором. В setup только нерактивные константы
*/

export default defineComponent({
  name: 'Main',
  components: {
    ValidationObserver,
    ValidationProvider,
    Paginate,
    FixFluidColGrid,
    HeaderChart,
    VTable,
    Loader,
    LoaderBtn,
    PassportModal,
    Stub,
    IcSearch,
    IcRefresh,
    IcReport
  },
  setup(props, context) {
    const cameraStatusList = CAMERA_STATUSES.map(s => ({
      id: s,
      name: `status.camera.${s}`
    }))

    const expoitationStatusList = EXPLOITATION_STATUSES.map(s => ({
      id: s,
      name: `status.exploitation.${s}`
    }))

    const maskIp = '9{1,3}.9{1,3}.9{1,3}.9{1,3}'
    const masIpRange = `${maskIp}[ - ${maskIp}]`
     
    const tableColumns = [
      {
        field: 'select-row',
        thClass: 'td-select',
        tdClass: 'td-select',
        sortable: false
      },
      {
        field: 'name',
        tdClass: 'td-name',
        width: '350px'
      },
      {
        field: 'status'
      },
      {
        field: 'statusExploitation'
      },
      {
        field: 'ip'
      },
      {
        field: 'ipServer'
      }
    ]

    const table = ref(null)

    let thEls = []

    onMounted(() => {
      thEls = table.value.$el.querySelectorAll('#vgt-table thead th')
    })

    const onTableScroll = e => {
      const { scrollTop } = e.target
      for (let i = 0; i < thEls.length; i++) {
        thEls[i].style.top = `${scrollTop}px`
      }
    }


    return {
      cameraStatuses: CAMERA_STATUSES,
      cameraStatusList,
      expoitationStatusList,
      maskIp,
      masIpRange,
      tableColumns,
      table,
      thEls,
      onTableScroll,
      getStatusDate: useStatusDate(context.root.$moment)
    }
  },

  data() {
    return {
      initialSort: null,
      cameraEdit: null,
      selectedAll: SELECT_ALL
    }
  },

  computed: {
    ...mapState({
      filters: state => state.cameras.filters,
      loading: state => state.cameras.loading,
      updateLoading: state => state.update.loading,
      list: state => state.cameras.list,
      statistic: state => state.cameras.statistic,
      pagination: state => state.cameras.pagination,
      filtersReport: state => state.reports.filters
    }),
    pageTotal() {
      const result = Math.ceil(this.pagination.total / this.pagination.limit)
      return result === 0 ? 1 : result
    },
    currentPage() {
      const result = Math.ceil(this.pagination.offset / this.pagination.limit) + 1
      return result === 0 ? 1 : result
    },
    chartLegend() {
      return CAMERA_STATUSES.map(status => {
        const statValue = this.statistic.value.find(value => value.status === status)
        if (statValue) {
          return statValue
        }
        return {
          status,
          total: 0
        }
      }).reverse()
    },
    isRowChecked() {
      return row => {
        if (this.filtersReport.selectedCameras === SELECT_ALL) {
          return true
        }
        const findedCamera = this.filtersReport.selectedCameras.find(camera => camera.id === row.id)
        if  (this.filtersReport.isSelectAll) {
          return findedCamera === undefined
        }
        return findedCamera !== undefined
      }
    },
    isSelectedAll() {
      if (this.filtersReport.selectedCameras === SELECT_ALL) {
        return true
      }
      if  (this.filtersReport.isSelectAll) {
        return Array.isArray(this.filtersReport.selectedCameras) &&
          this.filtersReport.selectedCameras.length === 0
      }
      /**
       * Если total === 0, то еще ничего не было загружено
       */
      if (this.pagination.total) {
        return Array.isArray(this.filtersReport.selectedCameras) &&
          this.filtersReport.selectedCameras.length === this.pagination.total
      }
    },
    chartData() {
      return CAMERA_STATUSES
      .map(status => {
        const statValue = this.statistic.value.find(value => value.status === status)
        if (statValue) {
          return statValue.total
        }
        return 0
      })
    },
  },
  created() {
    if (this.$store.state.cameras.sortParams) {
      this.initialSort = { ...this.$store.state.cameras.sortParams }
    } else {
      this.initialSort = {
        field: 'name',
        type: 'asc'
      }
    }
    this.onSearch = debounce(this.onSearch, 300)
  },
  beforeDestroy() {
    this.setPagination({
      ...this.pagination,
      offset: 0
    })
  },
  methods: {
    ...mapActions({
      getCameras: 'cameras/getCameras',
      updateCameras: 'update/updateCameras'
    }),
    ...mapMutations({
      setPagination: 'cameras/SET_PAGINATION',
      setFilter: 'cameras/SET_FILTER',
      setReportsFilter: 'reports/SET_FILTER',
      setSortParams: 'cameras/SET_SORT_PARAMS'
    }),
    /**
     * 
     * Запустится сразу при дефолтной сортировке 
     */
    onSortChange(event) {
      const sortData = { ...event[0] }
      /**
       * Ну вот потому, что так на беке...
       */
      if (sortData.field === 'ip' || sortData.field === 'status') {
        sortData.field = `${sortData.field}Cameras`
      }
      this.setSortParams(sortData)
      if (!this.updateLoading) {
        this.getCameras()
      }
    },
    onClickPage(number) {
      const pageOffset = (number - 1) * this.pagination.limit
      this.setPagination({
        ...this.pagination,
        offset: pageOffset
      })
      this.getCameras()
    },
    onRowClick(event) {
      const { target } = event.event
      if (!target.classList.contains('td-select')) {
        this.cameraEdit = event.row
        this.$nextTick(() => {
          this.$refs['passport-modal'].show()
        })
      }
    },
    onClosePassportModal() {
      this.cameraEdit = null
    },
    onSelectCamera(event, itemCamera) {
      let result = []
      const check = this.filtersReport.isSelectAll ? !event : event
      if (check) {
        result = this.filtersReport.selectedCameras === SELECT_ALL ? [itemCamera] :
          [...this.filtersReport.selectedCameras, itemCamera]
      } else {
        result = this.filtersReport.selectedCameras.filter(selectedCamera => selectedCamera.id !== itemCamera.id)
      }
      if (this.filtersReport.selectedCameras.length === this.pagination.total) {
        if (this.filtersReport.isSelectAll) {
          this.setReportsFilter({ key: 'selectedCameras', value: [] })
        } else {
          this.setReportsFilter({ key: 'selectedCameras', value: SELECT_ALL })
        }
      }
      this.setReportsFilter({ key: 'selectedCameras', value: result })
    },
    onSelectAll(event) {
      this.setReportsFilter({ key: 'isSelectAll', value: event })
      if (event) {
        this.setReportsFilter({ key: 'selectedCameras', value: SELECT_ALL })
      } else {
        this.setReportsFilter({ key: 'selectedCameras', value: [] })
      }
    },
    onFilterSubmit() {
      this.$refs['filterForm'].validate().then(result => {
        if (result) {
          this.refreshTableByFilters()
        }
      })
    },
    onSearch($event) {
      this.setFilter({ key: 'q', value: $event })
      this.refreshTableByFilters()
    },
    refreshTableByFilters() {
      this.setPagination({
        ...this.pagination,
        offset: 0
      })
      return this.getCameras()
    },
    setStatus(status) {
      this.setFilter({
        key: 'statusCameras',
        value: [{
          id: status,
          name: `status.camera.${status}`
        }]
      })
      this.refreshTableByFilters()
    }
  },
  watch: {
    updateLoading() {
      if (!this.updateLoading) {
        this.getCameras()
      }
    },
    '$i18n.locale'() {
      if (this.$refs.filterForm.flags.validated) {
        this.$refs.filterForm.validate()
      }
    }
  }
})