import { defineComponent, computed, ref } from '@vue/composition-api'
import debounce from 'debounce'
import { useDropdown, useFetchByScroll } from '@/components/composition/dropdown'
import { SELECT_ALL } from '@/consts/select-all-option'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import IcSelect from '@/assets/svg/ic-select.svg?inline'
import IcSearch from '@/assets/svg/ic-search.svg?inline'
import IcClear from '@/assets/svg/ic-clear.svg?inline'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

export default defineComponent({
  name: 'Select',
  components: {
    FixFluidColGrid,
    IcSelect,
    IcClear,
    IcSearch
  },
  props: {
    placeholder: {
      type: String
    },
    value: {
      /**
       * Array в случае если множественный селект
       */
      type: Object | Array | String
    },
    options: {
      type: Array ,
      /**
       * массив объектов с ключевыми полями,
       * соответствующими props fieldValue и fieldName
       * или массив строк
       */
      default: () => []
    },
    /**
     * Если флаг установлен, то считаем
     * что выделены все кроме тех что в значении
     * флаг можно сохранить при отправке
     * значения SELECT_ALL и снять, если отправляется
     * пустой массив.
     */
    isSelectAll: {
      type: Boolean,
      default: false
    },
    fieldValue: {
      type: String,
      default: 'id'
    },
    fieldName: {
      type: String,
      default: 'name'
    },
    optionAllName: {
      type: String
    },
    /**
     * функция возвращает объект action,
     * который возвращает объект вида { req, cancelToken }
     * req - Promise c oбъектом с полями:
     * list - список items
     * pagination: { total, limit, offset }
     */
    remoteFn: {
      type: Function
    },
    searchable: {
      type: Boolean,
      default: false
    },
    heightOptions: {
      type: String,
      default: 'auto'
    },
    showSelectedValueFunc: {
      type: Function
    },
    isTranslate: {
      type: Boolean,
      default: false
    },
    /**
     * Маска с разделителями, типа точек, пробелов и т.д.
     */
    searchMask: {
      type: String
    },
    error: {
      type: Boolean | String | Number,
      default: false
    },
    /**
     * нужно когда не multiple
     * и clearable true
     */
    emptyValue: {
      type: String,
      default: ''
    },
    clearable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, context) {
    const { dropdown, open, toggleOpen } = useDropdown()
    const i18n = useI18n()
    const isMultiple = Array.isArray(props.value) || props.value === SELECT_ALL
    const fetchByScrollLogic = useFetchByScroll(props.remoteFn, open)
    const currentOptions = props.remoteFn ? fetchByScrollLogic.currentOptions : ref(props.options)

    const {
      queryParamsSaved,
      fetchItems,
      resetResults,
      total,
      onScrollEnd,
      onScrollUp
    } = fetchByScrollLogic

    const search = ref('')

    let emptyMaskParts = []

    if (props.searchMask) {
      // eslint-disable-next-line no-useless-escape
      emptyMaskParts = props.searchMask.split(/[1-9aA,\{\}\[\]]+/g)
      .filter(s => s !== '')
      .map(s => `${s}_`)
    }

    const onSearch = debounce(() => {
      resetResults()
      let qSend = search.value
      if (props.searchMask) {
        let emptyIndexMask = -1
        for (let i = 0; i < emptyMaskParts.length; i++) {
          const emptyPart = emptyMaskParts[i]
          emptyIndexMask = search.value.indexOf(emptyPart)
          if (emptyIndexMask !== -1) {
            break
          }
        }
        if (emptyIndexMask !== -1) {
          qSend = search.value.substr(0, emptyIndexMask)
        }
      }
      queryParamsSaved.q = qSend
      fetchItems(queryParamsSaved)
    }, 300)

    const compareOptions = (left, right, isEqual = true) => {
      if (left && right) {
        if (typeof left === 'object') {
          return isEqual ? left[props.fieldValue] === right[props.fieldValue] :
            left[props.fieldValue] !== right[props.fieldValue]
        }
        return isEqual ? left === right :
          left !== right
      }
      return false
    }

    const isSelectedAll = computed(() => {
      if (props.value === SELECT_ALL) {
        return true
      }
      if  (props.isSelectAll) {
        return Array.isArray(props.value) &&
          props.value.length === 0
      }
    })

    const getOptionSelected = computed(() => option => {
      if (props.value === SELECT_ALL) {
        return true
      }
      if (isMultiple) {
        const valueItem = props.value.find(item => {
          return compareOptions(item, option)
        })
        if (props.isSelectAll) {
          return valueItem === undefined
        }
        return valueItem !== undefined
      }
      return compareOptions(props.value, option)
    })

    const getOptionName = option => {
      if (!option) {
        return ''
      }
      let name = ''
      if (typeof option === 'object') {
        name = option[props.fieldName]
      } else {
        name = option
      }
      if (props.isTranslate) {
        /**
         * из плюрализации берется первый вариант,
         * если что - переиграть логику с доп. props
         */
        return i18n.tc(name, 0)
      }
      return name
    }

    const valueName = computed(() => {
      if (isMultiple) {
        if (props.value === SELECT_ALL || props.value.length === 0) {
          return props.optionAllName ? props.optionAllName : i18n.t('selectsOptions.all')
        }
        if (props.isSelectAll && props.remoteFn) {
          /**
           * Здесь всегда выводим только количество,
           * даже если разница будет 1, мы не можем гарантировать
           * что элемент будет в массиве на данный момент
           * diff меньше нуля может быть только при перерисовке
           */
          const diff = total.value - props.value.length
          return diff <= 0 ? i18n.t('selectsOptions.all') : diff
        }
        if (props.value.length === 1) {
          if (props.isTranslate) {
            /*Не в прямую props.value[0], потому, что нужно для локализации*/
            const itemValueOption = currentOptions.value.find(o => {
              return compareOptions(o, props.value[0])
            })
            return getOptionName(itemValueOption)
          }
          return getOptionName(props.value[0])
        }
        return props.value.length
      }
      if (typeof props.value === 'object' && props.isTranslate) {
        /*Не в прямую props.value, потому, что нужно для локализации*/
        const itemValueOption = currentOptions.value.find(o => {
          return compareOptions(o, props.value)
        })
        return getOptionName(itemValueOption)
      }
      return getOptionName(props.value)
    })

    const onSelectAll = () => {
      if (props.value === SELECT_ALL) {
        context.emit('input', [])
      } else {
        context.emit('input', SELECT_ALL)
      }
    }

    const onSelectOption = option => {
      let result
      if (isMultiple) {
        if (props.value === SELECT_ALL) {
          if (props.isSelectAll) {
            result = [ option ]
          } else {
            result = currentOptions.value.filter(o => {
              return compareOptions(o, option, false)
            })
          }
        } else {
          const findedInValue = props.value.find(item => {
            return compareOptions(item, option)
          })
          if (findedInValue) {
            /*findedInValue будет либо строка либо ссылка на объект*/
            result = props.value.filter(itemValue => itemValue !== findedInValue)
          } else {
            result = [...props.value, option ]
          }
          if (props.remoteFn) {
            if (result.length === total.value) {
              if (props.isSelectAll) {
                context.emit('input', [])
              } else {
                context.emit('input', SELECT_ALL)
              }
              /* Здесь нужно выйти из функции */
              return
            } else if (result.length === 0 && props.isSelectAll) {
              context.emit('input', SELECT_ALL)
               /* Здесь нужно выйти из функции */
               return
            }
          } else {
            if (result.length === currentOptions.value.length) {
              context.emit('input', SELECT_ALL)
              /* Здесь нужно выйти из функции */
              return
            }
          }
          
        }
      } else {
        result = option
      }
      context.emit('input', result)
      if (!isMultiple) {
        open.value = false
      }
    }

    const onClear = () => {
      context.emit('input', props.emptyValue)
    }

    return {
      dropdown,
      open,
      toggleOpen,
      isMultiple,
      currentOptions,
      onScrollEnd,
      onScrollUp,
      search,
      onSearch,
      isSelectedAll,
      getOptionSelected,
      getOptionName,
      valueName,
      onSelectAll,
      onSelectOption,
      onClear,
      selectAll: SELECT_ALL
    }
  }
})