import { Doughnut, mixins } from 'vue-chartjs'

export default {
  extends: Doughnut,
  mixins: [mixins.reactiveProp],
  mounted () {
    this.renderChart(this.chartData, {
      cutoutPercentage: 35,
      layout: {
        padding: {
          top: -60,
          right: 60
        }
      },
      legend: {
        display: false
      }
    })
  }
}