import { mapActions, mapMutations } from 'vuex'
import { defineComponent } from '@vue/composition-api'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import LogoLogin from '@/assets/svg/logo-login.svg?inline'

export default defineComponent({
  name: 'Auth',
  components: {
    ValidationObserver,
    ValidationProvider,
    LogoLogin
  },
  data() {
    return {
      form: {
        login: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapActions({
      login: 'users/login',
    }),
    ...mapMutations({
      setGlobalLoading: 'SET_GLOBAL_LOADING'
    }),
    onSubmit() {
      this.$refs.authForm.validate().then(async result => {
        if (result) {
          this.setGlobalLoading(true)
          try {
            await this.login(this.form)
            this.setGlobalLoading(false)
            this.$router.push('/')
          } catch(e) {
            if (e.response && e.response.status === 403) {
              this.$refs.authForm.setErrors({
                password: this.$t('validation.validate_server_auth')
              })
            }
          } finally {
            this.setGlobalLoading(false)
          }
        }
      })
    }
  }
})