import { defineComponent } from '@vue/composition-api'
import Modal from '@/components/modal/modal.vue'

export default defineComponent({
  name: 'ConfirmModal',
  props: {
    isAlert: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Modal
  },
  methods: {
    show () {
      this.$refs.modal.show()
    },
    hide () {
      this.$refs.modal.hide()
    },
    apply() {
      this.$emit('apply')
      this.hide()
    },
    cancel() {
      this.$emit('cancel')
      this.hide()
    },
    onClose() {
      this.$emit('close')
    }
  }
})