import http from '@/services/http'
import moment from 'moment-timezone'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

const getDefaultFilters = () => ({
  isSelectAll: false,
  selectedCameras: [],
  rangeOfDate: null
})

export default {
  namespaced: true,
  state: {
    filters: getDefaultFilters(),
    loading: false,
    exportLoading: false
  },
  mutations: {
    SET_FILTER(state, { key, value }) {
      state.filters[key] = value
    },
    RESET_FILTERS(state) {
      state.filters = getDefaultFilters()
    },
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    SET_EXPORT_LOADING(state, exportLoading) {
      state.exportLoading = exportLoading
    }
  },
  actions: {
    async getReport({ state, commit }, type) {
      
      const optionsRequest = {
        params: {}
      }

      if (state.filters.rangeOfDate) {
        const timezoneOffset = moment().utcOffset()
        const format = 'YYYYMMDDTHHmmss'
        optionsRequest.params.rangeOfDate = [state.filters.rangeOfDate.startDate, state.filters.rangeOfDate.endDate]
          .map(date => date.clone().utc().format(format))
          .join('-')
        optionsRequest.params.timezoneOffset = timezoneOffset
      }

      if (Array.isArray(state.filters.selectedCameras) && state.filters.selectedCameras.length) {
        const idsStr = state.filters.selectedCameras
          .map(camera => camera.id)
          .join(',')

        if (state.filters.isSelectAll) {
          optionsRequest.params.notIds = idsStr
        } else {
          optionsRequest.params.ids = idsStr
        }
        
      }

      if (type) {
        optionsRequest.responseType  = 'blob'
        optionsRequest.params.locale = useI18n().locale.value
      }

      let url = 'report'
      if (type) {
        url = 'report' + `/${type === 'xlsx' ? 'excel' : type}`
        commit('SET_EXPORT_LOADING', true)
      } else {
        commit('SET_LOADING', true)
      }

      try {
        const res = await http.get(url, optionsRequest)
        if (type) {
          const url = window.URL.createObjectURL(new Blob([res]))
          const link = document.createElement('a')
          link.href = url;
          link.setAttribute('download', `report.${type === 'html' ? 'zip' : type}`)
          document.body.appendChild(link)
          link.click()
        }
        return res
      } finally {
        if (type) {
          commit('SET_EXPORT_LOADING', false)
        } else {
          commit('SET_LOADING', false)
        }
      }
    }
  }
}