import { getRequestWithCancel } from './utils'
import http from '@/services/http'
import { SELECT_ALL } from '@/consts/select-all-option'


const getDefaultFilters = () => ({
  statusCameras: [],
  rangeOfIpCameras: '',
  rangeOfIpServer: '',
  statusExploitation: [],
  q: ''
})

const getDefaultPagination = () => {
  return {
    total: 0,
    limit: 30,
    offset: 0
  }
}

export default {
  namespaced: true,
  state: {
    filters: getDefaultFilters(),
    loading: false,
    list: [],
    statistic: {
      value: [],
      countAllCameras: 0
    },
    pagination: getDefaultPagination(),
    sortParams: null,
    cancelToken: null
  },
  mutations: {
    SET_FILTER: (state, { key, value }) => {
      state.filters[key] = value
    },
    SET_DEFAULT_FILTERS: (state) => {
      state.filters = getDefaultFilters()
    },
    SET_LOADING: (state, loading) => {
      state.loading = loading
    },
    SET_LIST: (state, list) => {
      state.list = list
    },
    SET_STATISTIC: (state, statistic) => {
      state.statistic = statistic
    },
    SET_PAGINATION: (state, pagination) => {
      state.pagination = pagination
    },
    SET_SORT_PARAMS: (state, sortParams) => {
      state.sortParams = sortParams
    },
    SET_CENCEL_TOKEN: (state, cancelToken) => {
      state.cancelToken = cancelToken
    },
    UPDATE_SINGLE_CAMERA: (state, { data, isAll = true }) => {
      const indexUpdate = state.list.findIndex(item => item.id === data.id)
      if (indexUpdate !== -1) {
        if (!isAll) {
          Object.assign(state.list[indexUpdate], data)
        } else {
          state.list.splice(indexUpdate, 1, data)
        }        
      }
    }
  },
  actions: {
    getCameras({ commit, state }) {
      if (state.cancelToken) {
        state.cancelToken()
      }
      const { filters, pagination: {limit, offset} } = state

      const filtersToSend = {}

      const filtersKeys = Object.keys(filters)
      for (let i = 0; i < filtersKeys.length; i++) {
        const key = filtersKeys[i]
        const filterValue = filters[key]
        let sendValue
        switch (key) {
          case 'statusCameras': {
            if (filterValue !== SELECT_ALL && filterValue.length !== 0) {
              sendValue = filterValue.map(status => status.id).join(',')
            } else {
              sendValue = []
            }
            break
          }
          case 'rangeOfIpCameras': {
            const rangeParts = filterValue.replace(/ /g, '')
              .split('-')
              .filter(s => s !== '')
            /**
             * Например 172.217.22.23 - 172.217.22.23
             */
            if (rangeParts.length === 1) {
              rangeParts.push(rangeParts[0])
            }
            sendValue = rangeParts.join('-')
            break
          }
          case 'statusExploitation': {
            if (filterValue !== SELECT_ALL && filterValue.length !== 0) {
              sendValue = filterValue.map(status => status.id).join(',')
            } else {
              sendValue = []
            }
            break
          }
          default:
            sendValue = filterValue
        }

        filtersToSend[key] = sendValue
      }
      const queryParams = {
        ...filtersToSend,
        limit,
        offset
      }
      if (state.sortParams) {
        queryParams[`${state.sortParams.field}Sort`] = state.sortParams.type
      }
      const { req, cancelToken } = getRequestWithCancel('cameras', queryParams)
      commit('SET_CENCEL_TOKEN', cancelToken)
      commit('SET_LOADING', true)
      req
        .then(res => {
          const { list, statistic, pagination } = res
          commit('SET_LIST', list)
          commit('SET_STATISTIC', statistic)
          commit('SET_PAGINATION', pagination)
        })
        .finally(() => {
          commit('SET_LOADING', false)
        })
      return {
        req,
        cancelToken
      }
    },
    getCamerasShort(store, queryParams) {
      return getRequestWithCancel('cameras/short', queryParams)
    },
    getIpServer(store, queryParams) {
      return getRequestWithCancel('cameras/ip-server', queryParams)
    },
    async getNotVerifiedCameras(store, addParams) {
      const queryParams = {
        ...addParams
      }
      return getRequestWithCancel('unverified/passport', queryParams)
    },
    async createUpdatePassport({ commit }, { data: { id, data }, isCreate = true }) {
      const params = {
        stream: {
          ...data
        }
      }
      let res
      if (isCreate) {
        res = await http.post(`camera/passport/${id}`, params)
      } else {
        res = await http.put(`camera/passport/${id}`, params)
      }
      commit('UPDATE_SINGLE_CAMERA', { data: res })
      return res
    },
    async inOutExploitation({ commit }, { statusExploitation, id }) {
      let res
      switch (statusExploitation) {
        case 'notVerified':
          /**
           * ввести в эксплуатацию
           */
          res = await http.patch(`camera/passport/${id}`)
          break
        case 'introduced':
          /**
           * вывести из эксплуатаци
           */
          res = await http.delete(`camera/passport/${id}`)
          break
        default:
          return
      }
      commit('UPDATE_SINGLE_CAMERA', { data: res })
      return res
    },
    async updateCameraInfo({ commit }, id) {
      const res = await http.get(`camera/passport/${id}`)
      commit('UPDATE_SINGLE_CAMERA', { data: res.data, isAll: true })
      return res.data
    }
  }
}