import { mapActions, mapMutations, mapState } from 'vuex'
import { defineComponent } from '@vue/composition-api'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import Header from '@/components/header/header.vue'
import Loader from '@/components/loader/loader.vue'
import ConfirmAlertModal from '@/components/confirm-alert-modal/confirm-alert-modal.vue'
import Stub from '@/components/stub/stub.vue'

export default defineComponent({
  name: 'App',
  components: {
    FixFluidColGrid,
    VHeader: Header,
    Loader,
    ConfirmAlertModal,
    Stub
  },
  computed: {
    ...mapState({
      user: state => state.users.user,
      globalLoading: state => state.globalLoading,
      updateLoading: state => state.update.loading,
      updateWasError: state => state.update.wasError,
      errorConnection: state => state.errorConnection,
      serverError: state => state.serverError
    })
  },
  created() {
    const userStorage = localStorage.getItem('user')
    if (userStorage) {
      this.setUser(JSON.parse(userStorage))
    }
  },
  methods: {
    ...mapActions({
      cancelCheckJob: 'update/cancelCheckJob',
      updateCameras: 'update/updateCameras'
    }),
    ...mapMutations({
      setServerError: 'SET_SERVER_ERROR',
      setUser: 'users/SET_USER',
      setGlobalLoading: 'SET_GLOBAL_LOADING'
    })
  },
  watch: {
    updateLoading() {
      if (!this.updateLoading && !this.updateWasError) {
        this.$refs['refresh-alert-modal'].show()
      }
    },
    serverError() {
      if(this.serverError) {
        this.$refs['server-error-modal'].show()
      }
    },
    /**
     * изначально пользователя нет,
     * поэтому сюда он зайдет
     */
    user() {
      if (this.user) {
        this.setGlobalLoading(true)
        this.updateCameras().finally(this.setGlobalLoading(false))
      } else {
        this.cancelCheckJob()
      }
    }
  }
})