import TimeIntervalFormModal from '@/components/time-interval-form-modal/time-interval-form-modal.vue'
import { defineComponent, ref, computed, watch } from '@vue/composition-api'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

export default defineComponent({
  name: 'SelectInterval',
  components: {
    TimeIntervalFormModal
  },
  props: {
    value: {
      /**
       * { startDate, endDate - moment objects }
       */
      type: Object,
      default: null
    },
    placeholder: {
      type: String,
      default: ''
    },
  },
  setup(props, context) {
    const i18n = useI18n()
    const timeIntervalFormModal = ref(null)

    const showSelectedValueFunc = computed(() => value => {
      const dateFormat = i18n.locale.value === 'ru' ? 'DD.MM.YYYY (HH:mm)' : 'MM.DD.YYYY hh:mm A'
      if (value) {
        const { startDate, endDate } = value
        return `${startDate.format(dateFormat)} - ${endDate.format(dateFormat)}`
      }
      return ''
    })

    const options = [
      {
        unitCount: 15,
        unitName: 'minutes',
        id: 1,
        name: 'selectsOptions.timeInterval.last_min'
      },
      {
        unitCount: 1,
        unitName: 'hours',
        id: 2,
        name: 'selectsOptions.timeInterval.last_hour'
      },
      {
        unitCount: 1,
        unitName: 'day',
        id: 3,
        name: 'selectsOptions.timeInterval.last_day'
      },
      {
        unitCount: 1,
        unitName: 'week',
        id: 4,
        name: 'selectsOptions.timeInterval.last_week'
      },
      {
        id: 0,
        name: 'selectsOptions.timeInterval.choose'
      }
    ]

    const onInput = option => {
      if (option) {
        if (option.id !== 0) {
          context.emit('input', {
            value: option.id,
            startDate: context.root.$moment().subtract(option.unitCount, option.unitName),
            endDate: context.root.$moment()
          })
        } else {
          timeIntervalFormModal.value.show()
        }
      }
    }

    const setCustomTime = ({ startDate, endDate }) => {
      context.emit('input', {
        value: 0,
        startDate,
        endDate
      })
    }

    watch(i18n.locale, () => {
      if (props.value) {
        /**
         * поскольку в value экземпляры moment,
         * то их нужно обновлять в части locale отдельно
        */
        context.emit('input', {
          ...props.value,
          startDate: props.value.startDate.locale(i18n.locale.value),
          endDate: props.value.endDate.locale(i18n.locale.value)
        })
      }
    })

    return {
      timeIntervalFormModal,
      showSelectedValueFunc,
      options,
      onInput,
      setCustomTime
    }
  }
})