import { defineComponent, computed } from '@vue/composition-api'
import VuejsDatepicker from 'vuejs-datepicker'
import { useDropdown } from '@/components/composition/dropdown'
import {en, ru} from 'vuejs-datepicker/dist/locale'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

export default defineComponent({
  name: 'Datepicker',
  props: {
    value: {
      type: Date,
      default: null
    },
    format: {
      type: String,
      default: 'DD MMMM YYYY'
    },
    placeholder: {
      type: String,
      default: ''
    },
    error: {
      type: String | Boolean,
      default: ''
    },
    disabledDates: {
      type: Object
    }
  },
  components: {
    VuejsDatepicker
  },
  setup(props, context) {
    const i18n = useI18n()

    const { dropdown, open, toggleOpen } = useDropdown()
    const customFormatter = date => {
      if (!date) return ''
      return  context.root.$moment(date).format(props.format)
    }
    const onInput = event => {
      context.emit('input', event)
    }

    const currentLocale = computed(() => i18n.locale.value)

    return {
      dropdown,
      open,
      toggleOpen,
      customFormatter,
      onInput,
      locales: {
        en,
        ru
      },
      currentLocale
    }
  }
})
