import { extend, configure } from 'vee-validate'
import { required } from 'vee-validate/dist/rules';
import ipInt from 'ip-to-int'
import { i18n } from './i18n'

extend('required', required)

configure({
  defaultMessage: (field, values) => {
    if (field !== '{field}') {
      values._field_ = i18n.t(`fields.${field}`)
    }
    return i18n.t(`validation.${values._rule_}`, values)
  }
})

extend('invalid_ip_range', {
  validate: rangeTxt => {
    if (!rangeTxt) {
      return true
    }
    const validateIP = ip => {
      return (/^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(ip))
    }

    const ipArr = rangeTxt.split(' - ')

    if (!validateIP(ipArr[0])) {
      return false
    }
    if (validateIP(ipArr[0]) && ipArr[1] === undefined) {
      return true
    }
    if (ipArr[1] && !validateIP(ipArr[1])) {
      return false
    }
    return ipInt(ipArr[0]).toInt() <= ipInt(ipArr[1]).toInt()
  }
})

extend('invalid_date', {
  validate: momentDate => {
    return momentDate.isValid()
  }
})

extend('resolution', {
  validate: resolutionStr => {
    const [width, height] = resolutionStr
      .split(' : ')
      .map(item => +item.replace(/_/g, ''))
    
    return width && height
  }
})