import { defineComponent, reactive, computed, onBeforeMount, watch } from '@vue/composition-api'
import Datepicker from '@/components/datepicker/datepicker.vue'
import IcSelect from '@/assets/svg/ic-select.svg?inline'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

export default defineComponent({
  name: 'DateTime',
  components: {
    Datepicker,
    IcSelect
  },
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      // moment date
      type: Object,
      default: null
    },
    error: {
      type: String | Boolean,
      default: ''
    }
  },
  setup(props, context) {

    const i18n = useI18n()

    const meridiemList = [
      {
        id: 'am',
        name: 'am'
      },
      {
        id: 'pm',
        name: 'pm'
      }
    ]

    const dateFormatData = {
      en: {
          date: {
              format: 'MM/DD/YYYY'
          },
          time: {
              format: 'hh:mm A',
              mask: '99:99',
              placeholder: '—:—',
          }
      },
      ru: {
          date: {
              format: 'DD MMMM YYYY'
          },
          time: {
              format: 'HH:mm',
              mask: '99:99',
              placeholder: '—:—',
          }
      },
    }

    const controls = reactive({
      date: null,
      time: '',
      meridiemState: meridiemList[0]
    })

    const formatData = computed(() => {
      return dateFormatData[i18n.locale.value]
    })

    const isDayStateEnabled = computed(() => {
      return i18n.locale.value === 'en'
    })

    const setDefaultValues = () => {
      controls.date = props.value && props.value.isValid() ? props.value.toDate() : new Date()
      const momentDate = context.root.$moment(controls.date)
      const hours = context.root.$moment(controls.date).get('hour')
      controls.meridiemState = hours < 12 ? meridiemList[0] : meridiemList[1]
      controls.time = momentDate.format(formatData.value.time.format)
    }

    onBeforeMount(() => {
      setDefaultValues()
    })

    

    const sendDate = () => {
      const { date, time, meridiemState } = controls
      const timeStr = isDayStateEnabled.value ? `${time.split(' ')[0]} ${meridiemState.id}` : time
      const timeDate = context.root.$moment(timeStr, formatData.value.time.format, true)
      if (!timeDate.isValid()) {
        // invalid date
        context.emit('input', context.root.$moment(null))
      } else {
        const diff = timeDate.diff(timeDate.clone().startOf('day'))
        const dateMoment = context.root.$moment(date).startOf('day')
        dateMoment.add(diff)
        context.emit('input', dateMoment)
      }
    }

    watch(i18n.locale, () => {
      sendDate()
    })

    return {
      meridiemList,
      controls,
      formatData,
      isDayStateEnabled,
      sendDate
    }
  }
})

